import React, { useEffect, useRef } from 'react';
import './Message.css'
import moment from 'moment';

function ChatContent(props) {
    const user_id = props.user_id
    const message = props.message
    const messagesEndRef = useRef(null)

    const scrollToBottom = () => {
        messagesEndRef.current.scrollIntoView({
            behavior: "smooth",
            block: "nearest",
            inline: "start"
        })
    }
    useEffect(scrollToBottom, message)

    const showTime = () => {
        const nowDate = moment().format('YYYY-MM-DD');
        //var time = message.sendTime.replace(/T/, ' ').replace(/\..+/, '')
        var time = message.sendTime

        if (nowDate === time.slice(0, 10)) {
            return time.slice(11, 16)
        } else {
            return time
        }
    }

    const contents = (message) => {
        switch(message.type){
            //  
            case 0:
                return <div>
                    {message.contents}
                </div>

                
            //  
            case 1:
                return <div>
                    <p> {message.user_id} <span style={{fontWeight: 'bold'}}>[]</span> . </p>
                    <p style={{marginTop: '5px'}}>  '' !  </p>
                </div>
            //  
            case 2:
                return <div>
                    <p> {message.user_id} <span style={{fontWeight: 'bold'}}>[]</span>  </p>
                    <p style={{marginTop: '5px'}}>  '()' ! </p>
                </div>
              //  
              case 3:
                return(<div  >
                    <p> {message.user_id} <span style={{fontWeight: 'bold'}}>[]</span> . </p>
                    <p style={{marginTop: '5px'}}>    '' ! </p>
                </div>)
                 //  
            case 4:
                return(<div >
                    <p> {message.user_id} <span style={{fontWeight: 'bold'}}>[]</span>  </p>
                    <p style={{marginTop: '5px'}}>    ! </p>
                </div>)
                 //  
                   //  
            case 9:
                return <div>
                    <p>{message.user_id}  .</p>
                </div>


            case 10:
                return(<div >
                    <p> {message.user_id} <span style={{fontWeight: 'bold'}}>[]</span>  </p>
                    <p style={{marginTop: '5px'}}>    ! </p>
                </div>)
                //  
            case 11:
                return(<div >
                    <p> {message.user_id} <span style={{fontWeight: 'bold'}}>[]</span>  </p>
                    <p style={{marginTop: '5px'}}>   ! </p>
                </div>)

            case 7:
                return(<div >
                    <p> {message.user_id} <span style={{fontWeight: 'bold'}}>[]</span>  </p>
                    <p style={{marginTop: '5px'}}>   ! </p>
                </div>)
           case 21:
            return(<div  >
                <h4>    </h4>
                <p style={{marginTop: '5px'}}> please wait ... </p>
            </div>) 
                
            case 20:
                return (<img style={{maxWidth: '150px', maxHeight: '150px'}} 
                src={message.contents} onClick={() => { window.open(message.contents) }} />)
        }
    }
    const renderMsg = () => {
        var article = <div ref={messagesEndRef} />;
        var isMine = false;
        if (user_id === message.user_id) {
            isMine = true;
        }
        /* message.type === 0 || message.type === 20 */
        if (message.type === 0 || message.type === 20) {
            article = <div className={[
                'message',
                `${isMine ? 'mine' : ''}`, 'start', 'end'
            ].join(' ')}
            >
                <div className="bubble-container">
                    <div className="bubble">
                        {contents(message)}
                    </div>
                </div>
                <div className="timestamp">
                    {showTime()}
                </div>
                <div ref={messagesEndRef} />
            </div>
        } else {
            article =
                <div className={[
                    'message',
                    `${isMine ? 'mine' : ''}`, 'start', 'end'
                ].join(' ')}>
                    <div className="system-container">
                        <div className="system">
                            {contents(message)}
                        </div>
                    </div>
                    <div className="timestamp">
                        {showTime()}
                    </div>
                    <div ref={messagesEndRef} />
                </div>
        }

        return article
    }

    return (
        <>
            {renderMsg()}
        </>
    )
}

export default ChatContent
