
import React, { useState } from "react";
import axios from "axios";
import { withRouter } from "react-router-dom";
import {ip} from "../../store/ip"
import {TextField, Button, Container, Grid} from "@material-ui/core";
import SchoolCheck from "./schooltest";
import Alert from "../home/Alert";

function Register() {
  const [alertOption, setAlertOption] = useState({})
  const inputAlert = {open: true, text: "    .."}
  const registAlert = {open: true, text: " ! !", doAfter: () => {window.location.reload()}}

  const [id, setId] = useState("")
  const [pw, setPw] = useState("")
  const [rpw, setRpw] = useState("")
  const [phone, setPhone] = useState("")
  const [name, setName] = useState("")
  const [birth, setBirth] = useState("")
  const [school, setSchool] = useState("")
  const [address, setAddress] = useState("")
  const [nickName, setNickName] = useState("")
  const [checkRpw, setCheckRpw] = useState(true)

  //   
  const checkID = (id) => {
    // const idReg = /^[A-za-z0-9]{5,15}$/g
    setId(id)
  };

  //   
  const checkPassword = (pw) => {
    // var regExp = /^[a-zA-Z0-9]{5,20}$/;
    if(rpw !== "")
      setCheckRpw(rpw === pw)
    setPw(pw)
  };
  //   
  const checkRepassword = (rpw) => {
    setCheckRpw(rpw === pw)
    setRpw(rpw)
  };

  //    (   - )
  const checkPhoneNumber = (v) => {
    var num = v.replace(/[^0-9]/g, "").substring(0,11)
    if(num.length > 3)
      num = num.substring(0,3)+"-"+num.substring(3)
    if(num.length > 8)
      num = num.substring(0,8)+"-"+num.substring(8)
    setPhone(num)
  }
  const checkBirth = (v) => {
    // 2020-01-01 
    var num = v.replace(/[^0-9]/g, "").substring(0,8)
    setBirth(num)
  }

  /*,true false  */
  const SendSchool = (school) => {
    setSchool(school)
  }

  const onSubmit = (e) => {
    e.preventDefault();
    const newUser = {
      id: id,
      pw: pw,
      name: name,
      nickName: nickName,
      phone: phone.replace(/[^0-9]/g, ""),
      address: address,
      birth: birth.replace(/[^0-9]/g, ""),
      school: school,
    };
    //    
    if(!id || !pw || !name || !nickName || !phone || !address || !birth){
      setAlertOption(inputAlert)
      return
    }
  
    //  
    axios.post(ip+"/users/register", newUser)
      .then(() => {
        setAlertOption(registAlert)
      })
      .catch(e => {
        console.log(e)
      })
  };

  return (
    <Container component="main" maxWidth="xs">
      <Alert option={alertOption}/>
      <form noValidate onSubmit={onSubmit}>
        <Grid container spacing={2}>
          {/*  */}
          <Grid item xs={12}>
            <TextField value={id}
              onChange={(e) => checkID(e.target.value)}
              // onBlur={(e) => onCheckDuplicateID(e)}
              variant="outlined"
              required fullWidth
              label="ID"
            />
          </Grid>
          {/*  */}
          <Grid item xs={6}>
            <TextField
              value={pw}
              onChange={(e) => checkPassword(e.target.value)}
              variant="outlined"
              required fullWidth
              label=""
              type="password"
            />
          </Grid>
          {/*   */}
          <Grid item xs={6}>
            <TextField value={rpw} 
              onChange={(e) => checkRepassword(e.target.value)}
              variant="outlined"
              required fullWidth
              label=" "
              type="password"
              error={!checkRpw} helperText={checkRpw ? "" : " "}
            />
          </Grid>
          {/*  */}
          <Grid item xs={12}>
            <TextField value={nickName} 
              onChange={(e) => { setNickName(e.target.value)}}
              variant="outlined"
              required
              fullWidth
              label=""
            />
          </Grid>
          {/*  */}
          <Grid item xs={12}>
            <TextField value={name} 
              onChange={(e) => {setName(e.target.value)}}
              variant="outlined"
              required
              fullWidth
              label=""
            />
          </Grid>
          {/*   */}
          <Grid item xs={12}>
            <TextField  
              value={phone}
              onChange={(e) => checkPhoneNumber(e.target.value)}
              variant="outlined"
              required
              fullWidth
              label=" "
            />
          </Grid>
          {/*  */}
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              value={birth}
              onChange={(e) => { checkBirth(e.target.value)}}
              required
              fullWidth
              label=" "
              placeholder="( 2020-01-01)"
              type="birth"
            />
          </Grid>
          {/*  */}
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              value={address}
              onChange={(e) => {setAddress(e.target.value)}}
              required
              fullWidth
              label=" "
              placeholder="*(:  )"
              type="address"
            />
          </Grid>
                {/*   */}
          <Grid item xs={12}>
            <SchoolCheck SendSchool={SendSchool}/>
          </Grid>
          
          <Grid item xs={12}>
            <Button style={{height: "50px", fontSize: "15px"}}
              type="submit"
              fullWidth
              variant="contained"
              color="primary">
              
            </Button>
          </Grid>
        </Grid> 
      </form>
    </Container>
  );
}

export default withRouter(Register);